<style>
:root {
    --header-color: whitesmoke;
    --header-height: 60px;
}

* {
    padding: 0;
    margin: 0;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
}

.shadow {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}

a {
    text-decoration: none;
    color: gray;
}

a:hover {
    text-decoration: underline;
    opacity: 0.6;
}

nav {
    overflow: hidden;
}

nav>ol {
    float: left;
}


nav>ol>li {
    display: inline-block;
    margin-left: 5vw;
    font-size: 15px;
    /*  */
    height: 60px;
    line-height: 60px;
}

nav>ol>li:hover {
    opacity: 0.8;
}

.header-box {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: var(--header-height) !important;
    background-color: var(--header-color);
}

.main-box {
    position: absolute;
    width: 100%;
    height: calc(100% - var(--header-height));
    left: 0px;
    top: var(--header-height);
}

.aside-box {
    position: relative;
    padding: 0px 30px 20px 30px;
}

.main-right-box {
    position: relative;
}

.main-right-content {
    display: flex;
    flex-flow: row wrap;
    text-align: left;
    align-items: flex-start;
    width: 100%;
}

.doc-item {
    margin-top: 20px;
}

.doc-item-title {
    float: left;
}

.doc-item-sub {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 7px;
}

.main-link {
    list-style-type: none;
    margin-top: 5px;
}

.content-box {
    width: 200px;
    height: 300px;
    padding: 20px;
    margin-right: 20px;
    margin-bottom: 50px;
    flex: 0 0 20%;
    background-color: antiquewhite;
}

.content-box a {
    color: rgb(83, 83, 83);
}

.content-box a:hover {
    color: slategray;
    text-decoration: underline;
}
</style>


